<template>
  <div class="row" v-if="loaded">
    <div class="col-md-8 col-sm-12">
      <h4 class="mb-3 text-capitalize">{{ isNew ? "Add" : "Update" }} User</h4>
      <b-form class="form-signin" @submit.prevent="submit">
        <div class="row">
          <div class="col-md-6">
            <b-form-group label="First Name">
              <b-form-input type="text" placeholder="First Name" required autofocus autocomplete v-model="firstName" />
            </b-form-group>
          </div>
          <div class="col-md-6">
            <b-form-group label="Last Name">
              <b-form-input type="text" placeholder="Last Name" required autocomplete v-model="lastName" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
            <b-form-group label="Email">
              <b-form-input type="email" placeholder="Email address" required autocomplete v-model="email" :disabled="!isNew" />
            </b-form-group>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <b-form-group label="User Role">
              <b-form-select v-model="userRoleId" :options="list.userRoles" value-field="userRoleId" text-field="name" />
            </b-form-group>
          </div>
          <div class="col-md-6">
            <b-form-group label="Access Level">
              <b-form-select v-model="accessLevelId" :options="list.accessLevels" value-field="accessLevelId" text-field="name" />
            </b-form-group>
          </div>
        </div>
        <hr class="mb-4" />
        <b-button squared block variant="dark" type="submit">Save</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    userTypeId: String,
    userId: String,
  },
  computed: {
    isNew() {
      return this.userId == "new";
    },
  },
  async mounted() {
    await this.load();
  },
  methods: {
    async load() {
      try {
        let query = {
          includes: "filterlist.userroles,filterlist.accesslevels",
        };
        let url = `BackendServices/Users/${this.userId}`;
        let response = await this.$http.get(url, { query });

        this.firstName = response.firstName;
        this.lastName = response.lastName;
        this.email = response.email;
        this.userRoleId = response.userRoleId;
        this.accessLevelId = response.accessLevelId;
        this.list.userRoles = response.filterList.userRoles.filter(
          (x) => x.userTypeId == this.userTypeId
        );
        this.list.accessLevels = response.filterList.accessLevels;

        this.loaded = true;
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    async submit() {
      if (this.isNew) {
        await this.add();
      } else {
        await this.update();
      }
    },
    async add() {
      try {
        let body = {
          firstName: this.firstName,
          lastName: this.lastName,
          email: this.email,
          userTypeId: this.userTypeId,
          userRoleId: this.userRoleId,
          accessLevelId: this.accessLevelId,
        };
        let url = `BackendServices/Users`;
        await this.$http.post(url, { body });

        this.$router.push({ name: "users" });
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
    async update() {
      try {
        let body = {
          firstName: this.firstName,
          lastName: this.lastName,
          userTypeId: this.userTypeId,
          userRoleId: this.userRoleId,
          accessLevelId: this.accessLevelId,
        };
        let url = `BackendServices/Users/${this.userId}`;
        await this.$http.patch(url, { body });

        this.$router.push({ name: "users" });
      } catch (ex) {
        this.$event.$emit("error", ex.message);
      }
    },
  },
  data() {
    return {
      firstName: "",
      lastName: "",
      email: "",
      userRoleId: "",
      accessLevelId: "",
      list: {
        userRoles: [],
        accessLevels: [],
      },
      loaded: false,
    };
  },
};
</script>
